  line-height: 0;
}

.nav-link:hover {
  color: #7da0c3;
}

.nav-link svg {
  font-size: 20px;
  line-height: normal;
}

.dropdown-toggle::after {
  display: none;
}

.navicon {
  font-size: 1rem;
  height: 30px;
  display: inline-flex;
  justify-content: flex-end;
  align-items: center;
  color: #fff;
}

.navicon>* {
