}

.sidebarnav {
  background: #3e64ff;
  width: 280px;
  height: 100vh;
  max-height: calc(100vh - 60px);
  overflow: auto;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 60px;
  transition: 350ms;
  z-index: 10;
}

.cross {
  cursor: pointer;
}

.sidebarnav-show {
  right: 0;
}

.sidebarnav-hide {
  right: -100%;
}

.sidebarwrap {
